// Boolean attributes by specs
// https://html.spec.whatwg.org/multipage/indices.html#attributes-3
const HTML_BOOLEAN_ATTRIBUTES = [
  'allowfullscreen',
  'allowpaymentrequest',
  'async',
  'autofocus',
  'autoplay',
  'checked',
  'controls',
  'default',
  'defer',
  'disabled',
  'formnovalidate',
  'hidden',
  'ismap',
  'itemscope',
  'loop',
  'multiple',
  'muted',
  'nomodule',
  'novalidate',
  'open',
  'playsinline',
  'readonly',
  'required',
  'reversed',
  'selected',
  'typemustmatch',
]

// LUI-specific boolean attributes
const CUSTOM_BOOLEAN_ATTRIBUTES = [
  'icononly',
  'iconposafter',
  'indeterminate',
]

export default class LuiElement extends HTMLElement {
  static get is() {
    return ''
  }

  static cssClass() {
    return this.dasherize(this.name)
  }

  static modifierCssClass(name) {
    return this.cssClass() + '-' + name
  }

  static dasherize(name) {
    return name.replace(/[A-Z]+/g, (c, p) => (p > 0 ? '-' : '') + c.toLowerCase())
  }

  static isBooleanAttribute(name) {
    return LuiElement.BOOLEAN_ATTRIBUTES.includes(name)
  }
}

LuiElement.BOOLEAN_ATTRIBUTES = [
  ...HTML_BOOLEAN_ATTRIBUTES,
  ...CUSTOM_BOOLEAN_ATTRIBUTES,
]



